.App {
    width: 100vw;
    height: 100vh;

    display: flex;
    align-items: flex-start;
    justify-content: center;

    color: white;
}

.weather {
    width: 500px;
    height: 70vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 0;
    gap: 82px;
}

.weather-location {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: start;
    padding: 16px;
    flex: 1;
    position: relative;
}

.weather-info {
    display: flex;
    flex-direction: column;
}

.weather-city {
    font-weight: bold;
    font-size: 18px;
}

.weather-degrees {
    font-size: 98px;
    font-weight: bold;
}

.weather-state {
    display: flex;
    position: absolute;
    right: 0;
    top: 15%;
    transform: rotate(-90deg);
    padding: 8px;
    background: rgba(80, 80, 80, 0.55);
    border-radius: 4px;
}

.weather-state-info {
    font-weight: bold;
    font-size: 20px;
}

.weather-about {
    width: 100%;
    border-radius: 10px;
    /*background: rgba(255, 255, 255, 0.11);*/
    background: rgba(80, 80, 80, 0.55);
    padding: 18px;
    flex: 0;
    display: flex;
    justify-content: space-between;
    gap: 8px;
}

.bold {
    font-weight: bold;
    font-size: 22px;
}

.feels, .humidity, .wind {
    font-size: 18px;
    text-align: center;
}

.input {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 0;
    padding-left: 20px;
    border-radius: 25px;
    /*background: rgba(255, 255, 255, 0.11);*/
    background: rgba(80, 80, 80, 0.55);
    margin-top: 16px;
    border: 1px solid white;
    flex: 0;
}

.input-value {
    outline: none;
    border: none;
    color: rgba(255, 255, 255, 0.6);
    background: transparent;
    font-size: 18px;
}

.input-value::placeholder {
    color: rgba(255, 255, 255, 0.6);
}

.input-value:focus {
    color: rgb(255, 255, 255);
}

.input-value:focus::placeholder {
    color: rgb(255, 255, 255);
}


.input-icon {
    width: 60px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: white;
}

